<template>
    <div class="com-index-textarea" :style="{height: calcHeight}">
        <ul class="com-index-textarea__list" ref="indexUl">
            <li v-for="index in modelCount" :key="index">{{index}}</li>
        </ul>
        <div ref="indexTextarea" class="com-index-textarea__container">
            <textarea
                :rows="modelCount"
                class="com-index-textarea__text"
                :style="{minHeight: calcHeight}"
                wrap="off"
                v-model="model"
                @change="changeModel"
                @keyup="showHeight"
                autofocus>
            </textarea>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'ComIndexTextarea',
        props: {
            value: {
                type: String,
                default: ''
            },
            height: {
                type: Number,
                default: 100
            }
        },
        data () {
            return {
                model: this.value
            };
        },
        computed: {
            modelCount () {
                const count = this.model.split('\n');
                return count.length;
            },
            calcHeight () {
                return typeof this.height === 'number' ? `${this.height}px` : `${this.height}`;
            }

        },
        watch: {
            value (newValue) {
                this.model = newValue;
            }
        },
        methods: {
            /**
             * keyup
             */
            changeModel () {
                this.$emit('input', this.model);
            },
            /**
             * 
             */
            showHeight () {
                let indexHeight = this.$refs['indexUl'].scrollHeight;
                let textareaHeight = this.$refs['indexTextarea'].scrollHeight;
                // 
                // 1. 
                // 2. 
                // 3. 
                if (indexHeight !== textareaHeight) {
                    this.$refs['indexUl'].style.height = textareaHeight + 'px';
                }
            }
        }
    };
</script>

<style rel="stylesheet/scss" lang="scss">
    @import "index";
</style>
