<template>
    <div class="com-scroll-nav">
        <div class="js-com-scroll-nav__wrap" :style="{height: navHeight, width: navWidth}">
           <ul
                :class="{'is-fixed': navBarFixed}"
                :style="{
                    top: navTop,
                    right: navRight,
                    left: navLeft,
                    bottom: navBottom,
                    width: navBarFixed ? navFixedWidth : navWidth,
                    height: navBarFixed ? navFixedHeight : '100%'
                }">
                <li
                    v-for="(item, index) in navMenu"
                    :key="item.value"
                    class="com-scroll-nav__li"
                    :style="item.checked ? isActive : notActive"
                    @click="selectNav(item, index)">
                    {{item.label}}
                </li>
            </ul>
       </div>
       <slot></slot>
    </div>
</template>

<script>
import util from '~/util';
export default {
    name: 'ComScrollNav',
    props: {
        // 
        menu: {
            required: true,
            type: Array,
            default () {
                // {label: '', checked: false}
                return [];
            }
        },
        // 
        height: {
            type: [Number, String],
            default: 52
        },
        // 
        width: {
            type: [Number, String],
            default: '100%'
        },
        // id
        relativeName: {
            type: String,
            default: 'html'
        },
        // offsetParentoffsetParent
        // scrollTop
        extraFixed: {
            type: Number,
            default: 0
        },
        // 
        // offsetTop
        // offsetTop - 
        // coffsetTopsh
        //  s >= c - h  s + h >= c scrollToph
        // offsetParentpadding
        // extraScrollextraFixedextraFixed
        // 
        // extraFixed
        extraScroll: {
            type: Number,
            default: 0
        },
        needFixed: { // 
            type: Boolean,
            default: true
        },
        top: { // top
            type: [Number, String],
            default: 0
        },
        right: { // right
            type: [Number, String],
            default: 'auto'
        },
        left: { // left
            type: [Number, String],
            default: 'auto'
        },
        bottom: { // bottom
            type: [Number, String],
            default: 'auto'
        },
        fixedWidth: { // 
            type: [Number, String],
            default: 0
        },
        fixedHeight: { // 
            type: [Number, String],
            default: 0
        },
        color: { // 
            type: String,
            default: '#387af6'
        },
        stickyTop: { // stickytop
            type: [Number, String],
            default: 0
        },
        stickyLeft: { // stickyleft
            type: [Number, String],
            default: 0
        },
        stickyRight: { // stickyright
            type: [Number, String],
            default: 0
        },
        stickyBottom: { // stickybottom
            type: [Number, String],
            default: 0
        },
        useSticky: { // sticky
            type: Boolean,
            default: true
        }
    },
    data () {
        return {
            offsetTops: { // offsetTop
                navBar: 0
            },
            navBarFixed: false, // 
            scrollContainer: null, // 
            fixedHeightPx: 0, // px
            canUseSticky: false,
            notActive: {
                color: 'inherit',
                borderBottomColor: 'transparent'
            },
            navMenu: []
        };
    },
    watch: {
        menu: {
            handler (newValue) {
                this.navMenu = newValue.map(item => {
                    return {
                        label: item.label,
                        checked: item.checked || false
                    }
                })
            },
            immediate: true,
            deep: true
        }
    },
    computed: {
        // 
        navHeight () {
            return util.transPropString(this.height);
        },
        // 
        navWidth () {
            return util.transPropString(this.width);
        },
        // height
        navFixedHeight () {
            const height = this.fixedHeight ? this.fixedHeight : this.height;
            return util.transPropString(height);
        },
        // width
        navFixedWidth () {
            const width = this.fixedWidth ? this.fixedWidth : this.width;
            return util.transPropString(width);
        },
        navTop () {
            return util.transPropString(this.top);
        },
        navLeft () {
            return util.transPropString(this.left);
        },
        navBottom () {
            return util.transPropString(this.bottom);
        },
        navRight () {
            return util.transPropString(this.right);
        },
        // stickystickyToptop
        navStickyTop () {
            const value = this.stickyTop ? this.stickyTop : this.top;
            return util.transPropString(value);
        },
        navStickyLeft () {
            const value = this.stickyLeft ? this.stickyLeft : this.left;
            return util.transPropString(value);
        },
        navStickyRight () {
            const value = this.stickyRight ? this.stickyRight : this.right;
            return util.transPropString(value);
        },
        navStickyBottom () {
            const value = this.stickyBottom ? this.stickyBottom : this.bottom;
            return util.transPropString(value);
        },
        // extraScrollextraFixed
        scrollDeviation () {
            const extra = this.extraScroll ? this.extraScroll : this.extraFixed
            return this.fixedHeightPx + extra;
        },
        // 
        scrollTarget () {
            return this.relativeName.toLowerCase() === 'html' ? window : this.scrollContainer;
        },
        isActive () {
            return {
                color: this.color,
                borderBottomColor: this.color
            };
        }
    },
    methods: {
        /**
         * 
         */
        async selectNav (nav, index) {
            this.scrollContainer.scrollTop = this.offsetTops[`content${index}`] - this.scrollDeviation;
            console.log('selectNav -> this.scrollContainer.scrollTop', this.scrollContainer.scrollTop);
            await this.$nextTick();
            this.resetNavSelect();
            nav.checked = true;
        },
        resetNavSelect () {
            this.navMenu.forEach(item => {
                item.checked = false;
            });
        },
        /**
         * 
         */
        handleScroll () {
            const top = this.scrollContainer.scrollTop;
            // scrollTop
            const fixedBaseTop = top + this.scrollDeviation;
            const menuLength = this.navMenu.length;
            if (this.needFixed && !this.canUseSticky) {
                //  - 
                if ((top + this.extraFixed) >= this.offsetTops.navBar) {
                    this.navBarFixed = true;
                }
                //  - 
                if ((top + this.extraFixed) < this.offsetTops.navBar) {
                    this.navBarFixed = false;
                }
            }
            this.resetNavSelect();
            // 
            if (top + this.scrollContainer.clientHeight >= this.scrollContainer.scrollHeight) {
                this.navMenu[menuLength - 1].checked = true;
                return;
            }
            // 
            for (let i = 0; i < menuLength - 1; i++) {
                if (fixedBaseTop >= this.offsetTops[`content${i}`] && fixedBaseTop < this.offsetTops[`content${i + 1}`]) {
                    this.navMenu[i].checked = true;
                    return;
                }
            }
            if (fixedBaseTop >= this.offsetTops[`content${menuLength - 1}`]) {
                this.navMenu[menuLength - 1].checked = true;
                return;
            }
            this.navMenu[0].checked = true;
        },
        /**
         * offsetTopoffsetTop
         */
        calcTop (recalNav) {
            this.$nextTick(() => {
                recalNav && (this.offsetTops.navBar = document.querySelector('.com-scroll-nav .js-com-scroll-nav__wrap').offsetTop);
                this.navMenu.forEach((item, index) => {
                    this.offsetTops[`content${index}`] = this.$slots.default[index].elm.offsetTop;
                });
            });
        },
        hanldeResize () {
            this.calcTop(true);
        },
        /**
         * px
         * px
         */
        calcFixedHeightPx () {
            const div = document.createElement('div');
            div.style.position = 'fixed';
            div.style.height = this.navFixedHeight;
            document.body.appendChild(div);
            this.fixedHeightPx = div.offsetHeight;
            document.body.removeChild(div);
        },
        /**
        * sticky
        */
        isSupportSticky () {
            var prefix = ['-o-', '-ms-', '-moz-', '-webkit-', ''];
            var prefixValue = [];
            for (var i = 0; i < prefix.length; i++) {
                prefixValue.push(prefix[i] + 'sticky')
            }
            var element = document.createElement('div');
            var eleStyle = element.style;
            for (var j = 0; j < prefixValue.length; j++) {
                eleStyle.position = prefixValue[j];
            }
            return eleStyle.position;
        },
        /**
         * stickyfalsestickycss
         */
        validateSticky () {
            const supportStickyValue = this.isSupportSticky();
            if (supportStickyValue) {
                const navBarWrap = document.querySelector('.com-scroll-nav .js-com-scroll-nav__wrap');
                navBarWrap.style.position = supportStickyValue;
                navBarWrap.style.top = this.navStickyTop;
                navBarWrap.style.left = this.navStickyLeft;
                navBarWrap.style.right = this.navStickyRight;
                navBarWrap.style.bottom = this.navStickyBottom;
                return true;
            }
            return false;
        }
    },
    mounted () {
        this.calcFixedHeightPx();
        this.canUseSticky = this.useSticky && this.validateSticky();
        this.hanldeResize();
        this.scrollContainer = document.querySelector(this.relativeName);
        this.scrollTarget.addEventListener('scroll', this.handleScroll);
        // offsetTop
        window.addEventListener('resize', this.hanldeResize);
    },
    beforeDestroy () {
        this.scrollTarget.removeEventListener('scroll', this.handleScroll);
        window.removeEventListener('resize', this.hanldeResize);
    }
};
</script>

<style lang="scss">
@import 'index';
</style>
