<template>
    <span
        class="com-avatar"
        :style="{
            'background-color': backgroundColor,
            'background-image': `url(${src})`,
            'font-size': actualFontSize,
            width: actualSize,
            height: actualSize,
            'line-height': actualSize
        }">
        {{src ? '' : text}}
    </span>
</template>

<script>
import util from '~/util';
export default {
    name: 'ComAvatar',
    props: {
        content: { // src
            type: String,
            default: ''
        },
        fontSize: { // 
            type: [String, Number],
            default: '12px'
        },
        backgroundColor: { // 
            type: String,
            default: '#709ff7'
        },
        size: { // 
            type: [String, Number],
            default: '50px'
        },
        src: { // 
            type: String,
            default: ''
        },
        substr: { // content
            type: Number,
            default: 2
        }
    },
    computed: {
        text () {
            return this.content ? this.content.substr(0, this.substr) : '';
        },
        actualSize () {
            return util.transPropString(this.size);
        },
        actualFontSize () {
            return util.transPropString(this.fontSize);
        }
    }
};
</script>
<style lang="scss">
    @import "index";
</style>
